import { DOCUMENT, isPlatformBrowser, isPlatformServer } from '@angular/common';
import { Inject, Injectable, PLATFORM_ID } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class PlatformShieldService {

  constructor(
    @Inject(DOCUMENT) private readonly document: Document,
    @Inject(PLATFORM_ID) private readonly platformId: string,
  ) {}

  get nativeWindow(): Window {
    return globalThis.window || globalThis;
  }

  get nativeDocument(): Document {
    return this.document;
  }

  get mediaDevices(): MediaDevices {
    return this.nativeWindow.navigator.mediaDevices;
  }

  get pictureInPictureEnabled(): boolean {
    return 'pictureInPictureEnabled' in this.nativeDocument;
  }

  isPlatformBrowser(): boolean {
    return isPlatformBrowser(this.platformId);
  }

  isPlatformServer(): boolean {
    return isPlatformServer(this.platformId);
  }

}
