import { NgModule } from '@angular/core';
import { Routes, RouterModule, TitleStrategy } from '@angular/router';

import { PageTitleStrategy } from './services/page-title.strategy';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: '',
        pathMatch: 'full',
        title: $localize`:@@homePageTitle:Homepage`,
        loadChildren: () => import('./home').then(m => m.HomeModule),
      },
      {
        path: 'users',
        title: $localize`:@@usersPageTitle:Users`,
        loadChildren: () => import('./users').then(m => m.UsersModule),
      },
      {
        path: 'demo',
        title: 'Features demo',
        loadChildren: () => import('@playground/demo').then(m => m.DemoModule),
      },
    ],
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      paramsInheritanceStrategy: 'always',
      initialNavigation: 'enabledBlocking',
    }),
  ],
  providers: [
    {
      provide: TitleStrategy,
      useClass: PageTitleStrategy,
    },
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
