import { PlatformLocation } from '@angular/common';
import { ChangeDetectionStrategy, Component, Inject, LOCALE_ID } from '@angular/core';

import { PlatformShieldService } from '@playground/shared';

import { Language } from './interfaces/language';
import { MenuItem } from './interfaces/menu-item';

@Component({
  selector: 'playground-page-header',
  templateUrl: './page-header.component.html',
  styleUrls: ['./page-header.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PageHeaderComponent {

  readonly menuItems: MenuItem[] = [
    {
      link: '/',
      text: $localize`:@@menuItemHome:Home`,
    },
    {
      link: '/users',
      text: $localize`:@@menuItemUsers:Users`,
    },
  ];

  readonly languages: Language[] = [
    { id: 'en', label: 'English' },
    { id: 'he', label: 'עברית' },
    { id: 'ru', label: 'Русский' },
    { id: 'uk', label: 'Українська' },
  ];

  readonly currentLanguage: Language;

  private readonly defaultLanguage = 'en';

  constructor(
    private readonly platformLocation: PlatformLocation,
    @Inject(LOCALE_ID) private readonly localeId: string,
    private readonly platformShield: PlatformShieldService,
  ) {
    this.currentLanguage = this.getCurrentLanguage();
  }

  changeLanguage(languageId: string): void {
    if (languageId === this.currentLanguage.id) {
      return;
    }

    const baseHref = this.platformLocation.getBaseHrefFromDOM();
    const newBaseHref = `/${languageId}/`;
    const newUrl = this.platformShield.nativeWindow.location.pathname.replace(baseHref, newBaseHref);

    this.platformShield.nativeWindow.location.href = newUrl;
  }

  private getCurrentLanguage(): Language {
    const languageMap = this.languages.reduce((languageMap: Record<string, Language>, language: Language) => {
      languageMap[language.id] = language;

      return languageMap;
    }, {});

    return languageMap[this.localeId] || languageMap[this.defaultLanguage];
  }

}
